<template>
  <div class="private-access">
    <div class="form-info">
      <div class="py-4"></div>
      <div class="form">
        <v-alert
          v-if="loginHandle.message"
          :type="loginHandle.success ? 'success' : 'error'"
          >{{ loginHandle.message }}</v-alert
        >
        <div class="form">
          <v-text-field
            outlined
            placeholder="Credential"
            type="text"
            v-model="credentials.username"
          ></v-text-field>
          <v-text-field
            outlined
            placeholder="Password"
            type="password"
            v-model="credentials.password"
          ></v-text-field>
          <v-btn outlined width="100%" @click="login()">Access</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      credentials: {
        username: "",
        password: "",
      },
      loginHandle: {
        success: false,
        message: "",
      },
    };
  },
  methods: {
    login() {
      this.$http
        .post("/auth", {
          username: this.credentials.username,
          password: this.credentials.password,
        })
        .then((response) => {
          if (response.data[0].result) {
            this.loginHandle.success = true;
            const userInfo = {
              username: this.credentials.username,
              password: this.credentials.password,
            };
            localStorage.setItem("userInfo", JSON.stringify(userInfo));
            this.$router.push("/private/access/panel");
          } else {
            this.loginHandle.message = "Invalid Credentials";
            this.loginHandle.success = false;
          }
        })
        .catch(({ err }) => {
          return err;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
* {
  font-family: monospace;
}
.private-access {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .form-info {
    max-width: 300px !important;
  }
}
</style>
